:root {
  --blue: #00acdc;
  --black: #000;
  --white: #fff;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background: var(--white);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0;
  padding: 0;
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: var(--black);
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background: var(--blue);
  border-radius: 16px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--blue);
}
