:root {
  --blue: #00acdc;
  --black: #000;
  --white: #fff;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  padding: 0 50px;

  background: var(--white);
}

.nav__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.nav__item {
  margin: 0;
  padding: 0;

  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  display: flex;
  align-items: center;
  height: 60px;

  text-decoration: none;
  font-size: 12px;
  text-transform: uppercase;
  color: var(--black);
}

.is-active {
  color: var(--blue);
}

.is-active::after {
  content: '';
  height: 4px;
  background: var(--blue);
  border-radius: 16px;

  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.nav__link:hover {
  color: var(--blue);
}
